/* Оформление всей страницы */
/* Фиксируем полное покрытие экрана */
/* Полное покрытие экрана */
html, body {
    height: 100vh;
    width: 100vw;
    margin: 0;
    padding: 0;
    font-family: "Arial", sans-serif;
    background: radial-gradient(circle at top, #0b0c10, #1f2833);
    color: #66fcf1;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    overflow: hidden;
}

/* Контейнер с выравниванием по центру */
.container {
    max-width: 400px;
    width: 90%;
    background: rgba(20, 20, 30, 0.95);
    padding: 30px;
    border-radius: 15px;
    box-shadow: 0px 0px 30px rgba(102, 252, 241, 0.3);
    animation: fadeIn 0.8s ease-in-out;

    /* Центрирование */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    /* Высота контейнера, чтобы он не смещался */
    min-height: 60vh;
    position: relative;
}

/* Анимация появления */
@keyframes fadeIn {
    from {
        opacity: 0;
        transform: scale(0.9);
    }
    to {
        opacity: 1;
        transform: scale(1);
    }
}



/* Заголовок */
.title {
    font-size: 28px;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 2px;
    color: #66fcf1;
    text-shadow: 0 0 10px rgba(102, 252, 241, 0.8);
    margin-bottom: 10px;
}

/* Подзаголовок */
.subtitle {
    font-size: 16px;
    font-weight: lighter;
    color: rgba(102, 252, 241, 0.8);
    margin-bottom: 20px;
}

/* Меню */
.menu {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

/* Кнопки меню */
.menu-button {
    background: linear-gradient(135deg, #3a0ca3, #7209b7);
    color: white;
    font-size: 16px;
    padding: 12px 20px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    box-shadow: 0px 0px 10px rgba(114, 9, 183, 0.5);
}

.menu-button:hover {
    transform: scale(1.05);
    box-shadow: 0px 0px 20px rgba(114, 9, 183, 0.8);
}

/* Анимация появления */
@keyframes fadeIn {
    from {
        opacity: 0;
        transform: scale(0.9);
    }
    to {
        opacity: 1;
        transform: scale(1);
    }
}
